.App {
  text-align: center;
}

div{
  box-sizing: border-box;
}

.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.black-nav{
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.modal{
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.final_context{
  margin-top: 10px;
  padding-top: 3px;
  padding-bottom: 3px;
  background: black;
  font-size: 20px;
  color: white;
  text-align: center;
}